    position: absolute;
    top: 15px;
    left: 15px;
    width: 70px;
    height: auto;
} */

.btn-proceed {
    margin-top: 80px;
    margin-bottom: 100px;
    width: 200px;
    height: 60px;
    display: inline-block;
    padding: .9rem 1.25rem;
    border-radius: 10rem;
    border: none;
    color: #fff;
    background-color: #f8c007;
    text-transform: uppercase;
    font-size: 1.2rem;
    font-weight: 600;
    letter-spacing: 3px;
    -webkit-transition: all .4s;
    transition: all .4s;
}

.btn-proceed:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
    background-color: #f8c007;
}

.btn-proceed:focus {
    outline: none;
}

.switch-set-buttons {
    margin: auto;
    width: 310px;
    display: flex;
    justify-content: space-evenly;
}

.switch-set-buttons button{
    width: 150px;
    height: 50px;
    color: white;
    background-color: #242424;
    border: none;
    font-size: 1rem;
    font-weight: 600;
    -webkit-transition: all 0.7s;
    transition: all 0.7s;
}

.switch-set-buttons .btn-1 {
    border-radius: 25px 0px 0px 25px;
}

.switch-set-buttons .btn-2 {
    border-radius: 0 25px 25px 0;
}

.switch-set-buttons button:hover{
    cursor: pointer;
}

.switch-set-buttons button:focus{
    outline: none;
}

.switch-set-buttons .isactive {
    background-color: #f8c007;
    outline: none;
}

.navButtons {
    margin: auto;
    display: flex;
    width: 60vw;
    justify-content: space-between;
    -webkit-transition: all 1s;
    transition: all 1s;
    margin-bottom: 50px;
}


.navButtons button {
    cursor: pointer;
    border: none;
    background: none;
    outline: none;
    font-size: 5.5rem;
    color: black;
    opacity: 0.4;
}

.navButtons button:hover {
    opacity: 1;
}
.card {
    display: flex;
    margin: auto;
    position: relative;
    cursor: pointer;
    width: 530px;
    height: 340px;
    border-radius: 30px;
    box-shadow: 0px 15px 25px rgba(0,0,0,.8);
    z-index: 2;
}

.card-logo {
    position: absolute;
    height: 50px;
    width: auto;
    top: 30px;
    right: 30px;
}

.card-chip {
    position: absolute;
    height: 60px;
    width: auto;
    top: 30px;
    left: 30px;
}


.card-details-wrapper {
    font-family: 'Inconsolata', monospace;
    letter-spacing: 1px;
    position: relative;
    display: flex;  
    color: white;
    width: 100%;
    height: 100%;
    /* background-color: #ffffff60; */
}

.details-row {
    margin: 0 auto;
    width: 90%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
    /* background-color: green; */
}

.details-column {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    /* background-color: blue; */
    margin-bottom: 35px;
}

.card-number {
    font-size: 30px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    letter-spacing: 5px;
    width: 80%;
    text-shadow: 1px 1px #000000;
    display: flex;
    justify-content: space-between;
}

.digits-wrapper {
    width: 22%;
    text-align: left;
}

.details-card-holder,
.details-card-expires {
    font-size: 13px;
    opacity: 0.7;
    text-transform: uppercase;
    text-shadow: 1px 1px #000000;
}

.card-holder,
.card-expires {
    color: white;
    font-size: 18px;
    text-shadow: 1px 1px #000000;
    height: 1em;
}

@media screen and (max-width: 800px) {
    .card {
        width: 420px;
        height: 270px;
    }
    .card-number {
        font-size: 22px;
    }

    .details-card-holder,
    .details-card-expires { 
        font-size: 11px;
    }

    .card-holder,
    .card-expires {
        font-size: 15px;
    }
}

@media screen and (max-width: 430px) {
    .card {
        width: 310px;
        height: 200px;
    }
}




.form-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Exo 2', sans-serif;
}

.form-container h1{
    margin: 50px auto;
    font-size: 58px;
    color: white;
    text-shadow: 2px 1px black;
}

.btn-choose-template {
    margin-top: 80px;
    margin-bottom: 100px;
    width: 200px;
    height: 60px;
    display: inline-block;
    padding: .9rem 1.25rem;
    border-radius: 10rem;
    border: none;
    color: #fff;
    background-color: #f8c007;
    text-transform: uppercase;
    font-size: 1rem;
    font-weight: 600;
    letter-spacing: 3px;
    -webkit-transition: all .4s;
    transition: all .4s;
}


.btn-choose-template:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
    background-color: #f8c007;
}

.btn-return {
    position: absolute;
    left: 5vw;
    top: 5vh;
    background: none;
    border: none;
    outline: none;
}

.btn-return i {
    font-size: 70px;
    color: #f8c007;
    cursor: pointer;
}


.credit-card-form{
    position: absolute;
    top: 350px;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    width: 80vw;
    max-width: 600px;
    background-color: #ffffff;
    border-radius: 5px;
    margin: 20px auto;
    margin-bottom: 300px;
    box-shadow: 0px 15px 25px rgba(0,0,0,.8);
    display: flex;
    z-index: 1;
}


.inputs-wrapper {
    margin-top: 250px;
    padding: 15px;
    color: #222222;
    font-weight: 500;
}

.input-row {
    display: flex;
    width: 100%;
}

.input-column {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    padding: 5px;
}

label{
    margin-bottom: 5px;
    font-size: 14px;
    font-family: 'Nunito Sans', sans-serif;
    font-weight: 600;
}

input, select{
    background: none;
    border: 1px solid #22222240;
    border-radius: 5px;
    outline: none;
    width: 100%;
    margin: 5;
    height: 40px;
    vertical-align: bottom;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    font-size: 20px;
    color: white;
    font-weight: 600;
    -webkit-transition: all 0.4s;
    transition: all 0.4s;
    font-family: 'Nunito Sans', sans-serif;
    text-align: left;
    color: #2d2d2d;   
}

input:focus, select:focus{
    border: 1px solid #f8c007;
}

select {
    -webkit-appearance: none;
    text-align-last:center;
    cursor: pointer;
  }


.cardCVV {
    text-align: center;
}

.select-box {
    position: relative;
    width: 100%;

}

.btn-submit {
    margin: 30px auto;
    padding: 15px;
    width: 100%;
    height: 60px;
    font-size: 30px;
    font-weight: bold;
    border-radius: 5px;
    border: none;
    background-color: #f8c007;
    outline: none;
    cursor: pointer;
    -webkit-transition: background-color 0.4s;
    transition: background-color 0.4s;
}

.btn-submit:hover {
    background-color: #ca9b02;
}

.btn-submit:active {
    -webkit-transform: translateY(4px);
            transform: translateY(4px);
    box-shadow: none;
}

