input[type=number] {
    -moz-appearance: textfield;
  }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}


@media screen and (max-width: 800px) {
    input, select {
        font-size: 14px;
    }

    .btn-return {
        top: 1vh;
        left: 1vw;
    }

    .btn-return i {
        font-size: 30px;
    }

    .form-container h1{
        font-size: 40px;
    }

    .inputs-wrapper {
        margin-top: 100px;
    }
}

@media screen and (max-width: 430px) {
    .credit-card-form {
        top: 250px;
    }
}


